<template>
    <flex-column>
        <flex-column-header>
            <page-header type="realm">
                <template v-slot:left>
                    <h3>Photos <span class="text-muted">Realms</span>
                    </h3>
                </template>
                <template v-slot:right>
                    <div class="search" :class="{active:searchFocussed || search.length}">
                        <input v-model="search" @focus="searchFocussed = true" @blur="searchFocussed = false" placeholder="Filter realms by keyword" />
                        <div class="search-icon" @click="search = ''">
                            <fluro-icon icon="times" v-if="search.length" />
                            <fluro-icon icon="search" v-else />
                        </div>
                    </div>
                </template>
            </page-header>
        </flex-column-header>
        <fluro-page-preloader contain v-if="loading" />
        <template v-else>
            <template v-if="sorted.length > 1200">
                <fluro-paged-content :items="sorted" :per-page="12">
                    <template slot-scope="props">
                        <v-container pa-1 fluid grid-list-sm>
                            <v-layout row wrap>
                                <v-flex d-flex @click="view(realm)" xs12 sm6 md4 lg3 v-for="realm in props.page">
                                    <photo-realm-teaser :item="realm" />
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </template>
                </fluro-paged-content>
            </template>
            <template v-else>
                <flex-column-body scroll-parent>
                    <fluro-infinite-scroll :perPage="24" :startPage="1" :items="sorted">
                        <template slot-scope="props">
                            <v-container pa-1 fluid grid-list-sm>
                                <v-layout row wrap>
                                    <v-flex d-flex @click="view(realm)" xs12 sm6 md4 lg3 v-for="realm in props.page">
                                        <photo-realm-teaser :item="realm" />
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </template>
                    </fluro-infinite-scroll>
                </flex-column-body>
            </template>
        </template>
    </flex-column>
</template>
<script>
/////////////////////////////////////////////////////

import PhotoRealmTeaser from '@/components/photos/PhotoRealmTeaser.vue';
import { FluroInfiniteScroll, FluroPagedContent } from 'fluro-vue-ui'
/////////////////////////////////////////////////////

import BreadcrumbMixin from '@/mixins/BreadcrumbMixin'

export default {
    mixins: [BreadcrumbMixin],
    components: {
        FluroInfiniteScroll,
        FluroPagedContent,
        PhotoRealmTeaser,
    },
    data() {
        return {
            loading: true,
            search: '',
            searchFocussed: false,
            debouncedSearch: this.search,
        }
    },
    computed: {
        grouped() {
            var self = this;
            return _.chain(self.filtered)
                .orderBy(function(realm) {
                    return realm.title;
                })
                .reduce(function(set, realm) {

                    var definitionName = realm.definition || 'realm';
                    var title = self.$fluro.utils.readable(definitionName, false, 'realm');

                    var existing = set[definitionName];

                    if (!existing) {
                        existing = set[definitionName] = {
                            title,
                            definitionName,
                            realms: [],
                        }
                    }

                    existing.realms.push(realm);

                    return set;
                }, {})
                .values()
                .orderBy(function(group) {
                    return group.title;
                })
                .value();
        },
        routeKey() {
            return this.$route.name.split('.')[0];
        }
    },
    methods: {
        view(realm) {
            var source = this.$fluro.utils.getStringID(realm);
            this.$router.push({
                name: 'photos.realm',
                params: {
                    source,
                }
            })
        },
        chunked(items, number) {
            return _.chunk(items, number);
        }
    },
    created() {},
    watch: {
        search: _.debounce(function(newValue) {
            this.debouncedSearch = newValue;
            // this.refine();
        }, 500),
    },
    computed: {
        sorted() {
            var self = this;
             return _.orderBy(self.filtered, function(item) {
                return String(item.title).toLowerCase();
            });
        },
        filtered() {



            var self = this;
            var filtered = self.items;

            if (!self.debouncedSearch || !self.debouncedSearch.length) {
                return filtered;
            }

            ////////////////////////////////////////////////

            //Get the search keywords
            var searchKeywords = String(self.debouncedSearch).toLowerCase().trim();
            var searchPieces = searchKeywords.split(' ');







            ////////////////////////////////////////////////

            return _.filter(filtered, function(item) {

                var itemTitle = String(item.title).trim().toLowerCase();

                var exactMatch = _.includes(itemTitle, searchKeywords);
                if (exactMatch) {
                    return true;
                }

                var pieces = itemTitle.split(' ');
                pieces = pieces.concat(item.keywords);


                var match = _.every(searchPieces, function(partial) {
                    return _.some(pieces, function(word) {
                        return _.includes(word, partial);
                    })
                })

                if (match) {
                    return true;
                }
            });


        }
    },
    asyncComputed: {
        items: {
            default: [],
            get() {
                var self = this;
                self.loading = true;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/photos/realm/list', {
                        params: {
                            // limit: 10000,
                        }
                    }).then(function(res) {


                        resolve(res.data);
                        self.loading = false;
                    }, function(err) {
                        reject(err)
                        self.loading = false;
                    });
                })
            }
        },

    },
}
</script>
<style lang="scss">
.photo-tag {
    background: rgba(#000, 0.1);
    border-radius: 100px;
    display: block;
    padding: 10px;

    .fluro-image {
        width: 25px;
        float: left;
        border-radius: 100px;
    }
}
</style>